self.onmessage = async (e) => {
  const window = globalThis;
  window.window = globalThis as any;
  if (!e.data) return;
  if (typeof e.data !== 'object') return;
  if (e.data.to !== 'wasm2048') return;
  const wasm2048 = await import('wasm2048');
  const { requestId, data, action, to } = e.data;
  const end = (response: any) => {
    self.postMessage({ requestId, from: to, data: response });
  };

  try {
    switch (action) {
      case 'prove_2048': {
        end(wasm2048.prove_2048(data));
        break;
      }
      default:
        throw new Error('unknow action');
    }
  } catch (error) {
    self.postMessage({ requestId, from: to, data: null, error });
  }
};
export {};
